const CollectionsPanel = ({ collections, active, hasMore, page, getCollections, domain, updateState, updateProductState }) => {

    function getListClassName(id) {
        var classNameList = "flex items-center justify-between mb-1 rounded-lg px-4 py-1.5 cursor-pointer hover:bg-red-100 hover:text-red-600 transition duration-150 ease-in-out";
        if (id === active) {
            classNameList += " text-red-600 bg-red-200";
        }

        return classNameList;
    };

    function getCountClassName(id) {
        var className = "font-semibold text-gray-400 text-sm";
        if (id === active) {
            className += " text-red-600 font-bold";
        }

        return className;
    }

    function toggleChangeCollection(handle) {
        if (handle !== active) {
            updateState({ active: handle });
            updateProductState({ products: [] });
        }
    }

    return (
        <div className="sticky top-0 w-72 h-auto">
            <div className="flex items-center space-x-2">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                </svg>
                <h2 className="font-bold text-xl">Collections</h2>
            </div>
            <ul className="relative list-none mt-3">
                {collections.map(item => (
                    <li key={item.id} className={getListClassName(item.handle)} onClick={() => toggleChangeCollection(item.handle)}>
                        <span className="mr-4 text-base font-bold">{item.title.replace("-", " ")}</span>
                        <span className={getCountClassName(item.handle)}>{item.products_count}</span>
                    </li>
                ))}
            </ul>
            {hasMore?(
                <a className="mt-3 text-blue-600 px-2 font-semibold text-base cursor-pointer hover:underline transition duration-150 ease-in-out" onClick={() => getCollections(domain, page + 1)}>Show more</a>
            ):null}
        </div>
    );
};

export default CollectionsPanel;
