import ProductCard from "./ProductCard";

const ProductsPanel = ({ products, loading, page, hasMore, updateState, getProducts, handle, domain }) => {
    function togglePrevPage() {
        updateState({ products: [] });
        getProducts(domain, handle, page-1);
    }

    function toggleNextPage() {
        updateState({ products: [] });
        getProducts(domain, handle, page+1);
    }

    return (
        <div className="w-full">
            <div className="flex items-center space-x-2">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z" />
                </svg>
                <h2 className="font-bold text-xl">Products</h2>
            </div>
            <ol className="relative list-none grid grid-cols-product-sm 2xl:grid-cols-product-md gap-9 mt-6 transition ease-in-out duration-150">
                {products.map(item => (
                    <ProductCard key={item.id} {...item} />
                ))}
            </ol>
            {loading ? (
                <div className="w-full">
                    <div className="w-full flex items-center justify-center h-14">
                        <div className="flex items-center justify-center w-12 h-12 rounded-lg">
                            <svg className="animate-spin h-9 w-9 text-gray-500" fill="none" viewBox="0 0 24 24">
                                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                            </svg>
                        </div>
                    </div>
                </div>
            ) : null}
            {page > 1 || hasMore ? (
                <div className="flex items-center space-x-3 justify-center mt-4">
                    {page == 1 || <button type="button" className="bg-gray-200 rounded-lg px-3 py-2 text-gray-600 hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-300 transition ease-in-out duration-150" onClick={togglePrevPage}>
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                        </svg>
                    </button>}
                    {!hasMore || <button type="button" className="bg-gray-200 rounded-lg px-3 py-2 text-gray-600 hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-300 transition ease-in-out duration-150" onClick={toggleNextPage}>
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3" />
                        </svg>
                    </button>}
                </div>
            ) : null}
        </div>
    );
};

export default ProductsPanel;
