import { useContext } from "react";
import Header from "./Header";
import CollectionsPanel from "./CollectionsPanel";
import ProductsPanel from "./ProductsPanel";
import CollectionsSkeleton from "./CollectionsSkeleton";

import Context from "../utils/context";

const Index = () => {
    const { collectionState, productState, domain, page } = useContext(Context)

    return (
        <>
            <Header />
            <div className="px-2 pb-8">
                {page==="stores"?(
                    <p>Stores</p>
                ):(
                    <div className="flex">
                        {collectionState.loading ? (
                            <div className="flex item-center w-72">
                                <CollectionsSkeleton />
                            </div>
                        ) : (!collectionState.collections.length ? (
                            <div className="h-20 w-full flex flex-col flex-items justify-center text-center">
                                <h2 className="text-lg font-bold">Store not found.</h2>
                                <p className="text-base">Domain is not a valid shopify store.</p>
                            </div>
                        ) : (
                            <>
                                <div className="flex item-center w-72">
                                    <CollectionsPanel {...collectionState} domain={domain} updateProductState={productState.updateState}/>
                                </div>
                                <div className="flex-1 ml-8">
                                    <ProductsPanel {...productState} domain={domain} handle={collectionState.active} />
                                </div>
                            </>
                        ))}
                    </div>
                )}
            </div>
        </>
    )
}

export default Index;
