const ProductCard = ({title, images, variants}) => {
    var image = "";
    if (images && images.length) {
        image = images[0].src;
    }

    var price = "$0.00";
    if (variants && variants.length) {
        price = "$" + variants[0].price;
    }

    return (
        <li>
            <div className="cursor-pointer rounded-xl">
                <div className="relative overflow-hidden h-0 rounded-xl pb-75p mt-3 mb-2">
                    <figure className="overflow-hidden m-0 h-0 pb-75p bg-gray-200">
                        <img className="w-full h-auto" src={image} alt={title} sizes="(max-width: 500px) 320px, (min-width: 501px) and (max-width: 615px) 600px, 400px" width="330" height="247"/>
                    </figure>
                </div>
                <h2 className="text-lg font-bold">{title}</h2>
                <p className="text-gray-700 text-base font-semibold">{price}</p>
            </div>
        </li>
    )
}

export default ProductCard;
