import { useContext, useState } from "react";

import Context from "../utils/context";

const Header = () => {
    const { setDomain, collectionState, domain, productState, setPage, scraping, setScraping } = useContext(Context)
    const [domainString, setDomainString] = useState("");

    function handleDomainChange(e) {
        setDomainString(e.target.value);
    }

    function toggleSearch() {
        collectionState.updateState({ collections: [], page: 1, active: "" });
        productState.updateState({ products: [], page: 1, active: "" });
        if (!domainString) {
            setPage("stores");
            return;
        }
        setScraping(true);
        setPage("collections");
        if (domainString === domain) {
            collectionState.getCollections(domain, 1);
        } else {
            setDomain(domainString)
        }
    }

    return (
        <header className="sticky top-0 z-30 bg-opacity-50 h-20 bg-white backdrop-filter backdrop-blur  firefox:bg-opacity-90">
            <div className="max-w-7xl lg:mx-auto mx-6">
                <div className="flex items-center h-20 space-x-10">
                    <div className="flex items-center">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-1 text-red-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 21h7a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v11m0 5l4.879-4.879m0 0a3 3 0 104.243-4.242 3 3 0 00-4.243 4.242z" />
                        </svg>
                        <h2 className="text-2xl font-bold">Shopify store scraper</h2>
                    </div>
                    <div className="flex-1 flex items-center space-x-4">
                        <div className="relative rounded-lg flex-1">
                            <div className="absolute z-10 inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                            <span className="text-gray-500">
                                <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                    <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
                                </svg>
                            </span>
                            </div>
                            <input type="search" name="domain" id="domain" className="relative w-full inline-flex items-center font-semibold pl-10 pr-4 py-2 border border-transparent rounded-md text-gray-800 bg-gray-100 placeholder-head-100::placeholder md:placeholder-text-gray-400::placeholder focus:outline-none focus:ring-red-300 focus:border-red-300 transition duration-150 ease-in-out" placeholder="Search for domain ex:partakefoods.com" value={domainString} onChange={handleDomainChange} />
                        </div>
                        <button type="button" id="btnSearchEcom" className="relative inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-500 text-base font-semibold text-white hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-300 transition ease-in-out duration-150" rel="nofollow" onClick={toggleSearch} disabled={scraping} >
                            {scraping ? (
                                <div>
                                    <span className="invisible">Scrape</span>
                                    <div className="absolute top-0 left-0 w-full h-full items-center justify-center">
                                        <div className="w-full h-full flex items-center justify-center">
                                            <svg className="animate-spin h-6 w-6 text-white" fill="none" viewBox="0 0 24 24">
                                                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                            </svg>
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <span className="Label">Scrape</span>
                            )}
                        </button>
                    </div>
                </div>
            </div>
        </header>
    );
}

export default Header;
