import { useReducer } from "react";
import axios from "axios";

const ACTIONS = {
    FETCH_COLLECTIONS: "fetch-collections",
    GET_COLLECTIONS: "get-collections",
    ERROR: "error",
    UPDATE: "update",
    UPDATE_STATE: "update-state"
};

const initialState = {
    loading: false,
    collections: [],
    error: "",
    active: "",
    limit: 15,
    hasMore: true,
    page: 1
};

function reducer(state, action) {
    switch (action.type) {
        case ACTIONS.FETCH_COLLECTIONS:
            return {
                ...state,
                loading: true,
            };
        case ACTIONS.GET_COLLECTIONS:
            return {
                ...state,
                loading: false,
                ...action.payload,
            };
        case ACTIONS.ERROR:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
                collections: [],
            };
        case ACTIONS.UPDATE:
            var data = {
                ...state,
                loading: false,
                ...action.payload
            }
            data.collections = [...state.collections, ...action.payload.collections];
            return {...state, ...data};
        case ACTIONS.UPDATE_STATE:
            return {
                ...state,
                ...action.payload
            }
        default:
            return state;
    }
};

export default function useCollections(setScraping) {
    const [state, dispatch] = useReducer(reducer, initialState);

    function getCollections(domain, p=1) {
        if (!domain) {
            dispatch({ type: ACTIONS.GET_COLLECTIONS, payload: { collections: [] } });
            return;
        }
        var page = state.page;
        if (p) {
            page = p;
        }

        dispatch({ type: ACTIONS.FETCH_COLLECTIONS });

        axios.get(`https://${domain}/collections.json?limit=${state.limit}&page=${page}`)
            .then(({ data }) => {
                setScraping(false);
                if (data.collections.length) {
                    var payload = {
                        collections: data.collections,
                        page
                    }
                    payload.active = data.collections[0].handle;
                    if (data.collections.length === state.limit) {
                        payload.hasMore = true;
                    } else {
                        payload.hasMore = false;
                    }
                    dispatch({type: ACTIONS.UPDATE, payload});
                }
            })
            .catch(error => {
                setScraping(false);
                dispatch({ type: ACTIONS.ERROR, payload: {error} });
            })
    }

    function updateState(newState) {
        dispatch({
            type: ACTIONS.UPDATE_STATE,
            payload: newState
        });
    }

    return {...state, getCollections, updateState}
};
