import { useReducer, useEffect } from "react";
import axios from "axios";

const ACTIONS = {
    FETCH_PRODUCTS: "fetch-products",
    GET_PRODUCTS: "get-products",
    ERROR: "error",
    UPDATE_STATE: "update-state"
};

const initialState = {
    loading: false,
    products: [],
    error: "",
    limit: 25,
    page: 1,
    hasMore: true,
};

function reducer(state, action) {
    switch (action.type) {
        case ACTIONS.FETCH_PRODUCTS:
            return {
                ...state,
                loading: true
            };
        case ACTIONS.GET_PRODUCTS:
            var data = {
                ...state,
                loading: false,
                ...action.payload
            }
            data.products = [...state.products, ...action.payload.products];
            return {...state, ...data};
        case ACTIONS.ERROR:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
                products: [],
            };
        case ACTIONS.UPDATE_STATE:
            return {
                ...state,
                ...action.payload
            }
        default:
            return state;
    }
};

export default function useProducts() {
    const [state, dispatch] = useReducer(reducer, initialState);

    function getProducts(domain, handle, p=1) {
        if (!domain) {
            dispatch({ type: ACTIONS.GET_PRODUCTS, payload: { products: [] } });
            return;
        }

        var page = state.page;
        if (p) {
            page = p;
        }

        dispatch({ type: ACTIONS.FETCH_PRODUCTS });

        axios.get(`https://${domain}/collections/${handle}/products.json?limit=${state.limit}&page=${page}`)
            .then(({ data }) => {
                var payload = {
                    products: data.products,
                    page
                };

                if (data.products.length === state.limit) {
                    payload.hasMore = true;
                } else {
                    payload.hasMore = false;
                }

                dispatch({
                    type: ACTIONS.GET_PRODUCTS,
                    payload
                });
            })
            .catch(error => {
                dispatch({ type: ACTIONS.ERROR, payload: {error} });
            })
    }

    function updateState(newState) {
        dispatch({
            type: ACTIONS.UPDATE_STATE,
            payload: newState
        });
    }

    return {...state, getProducts, updateState}
};
